import React, { Component } from "react"
class CountryList extends Component {
  renderCountry = each => {
    return (
      <li
        className="items"
        key={each.currency}
        onClick={() => this.props.handleClick(each.currency)}
      >
        {each.country}
      </li>
    )
  }
  render() {
    return (
      <>
      <input type="text" value={this.state.country} onc/>
      <ul>{this.props.countries.map(each => this.renderCountry(each))}</ul>
      </>
    )
  }
}
export default CountryList
